.maincomment{
    margin:auto;
}
.inputbox{
    padding:1rem;
    display: block;
    justify-content: center;
    align-items: center;
    margin-left:auto;
}

.textarea,.textareapic{
    /* padding:1rem; */
    /* resize:none; */
    height:1.5rem;
    /* transition:0.25s ease-in-out; */
    border-radius: 1rem;
    margin:1rem 0.5rem;
    width:20rem;
    padding-left: 0.5rem;
    resize: none;
    
}

@media (max-width:700px) {
    .textarea,.textareapic{
        width: 30vw;
    }
    
}
.textarea:focus{
    /* height:35vh; */
    /* border:none; */
    /* border-radius: 1rem; */
    outline:none;
    
    
}

.textareapic:focus{
    outline:none;
    
}

.textareahide{
    display:none;
}

.submitbutton{
    position: relative;
    top:0;
    height:2rem;
    margin:0rem 0.5rem;
}

