.publicButton{
    display: flex;
    justify-content: space-around;
    
}

.publicButtons{
    /* background:#4d90b8;
    color:rgb(170, 170, 170); */
    
    border-top-left-radius: 0.4rem;
    border-top-right-radius: 0.4rem;
    font-weight: bold;
    /* border:2px solid #4d90b8;
    border-bottom:2px solid #4d90b8; */
    outline: none;
    padding:0.3rem 0.5rem;
}

.disabledButtons{
    /* background:rgba(8, 185, 255,0.8);
    color:white; */
    font-weight: bold;
    border:none;
    border-top-left-radius: 0.4rem;
    border-top-right-radius: 0.4rem;
    padding:0.3rem 0.5rem;
}

.publicButtons:active{
    border-style:outset;
}
.commentsection{
    display:block;
    /* background:linear-gradient(340deg ,rgba(31, 94, 150,0.1),rgba(8, 185, 255,0.8)); */
    /* background-image: url('../../images/mainbackground2.png'); */
    /* background-size: 40%; */
    /* background-repeat: no-repeat; */
    /* background-position: center; */
    height: 75vh;
    /* border-top-left-radius: 1rem;
    border-top-right-radius: 1rem; */
    border-radius: 1rem;
    /* border:4px solid rgba(8, 185, 255,0.8); */
    width: 35rem;
    overflow-y: scroll;
    /* border-top:4px dashed black; */
    /* background-attachment: scroll; */
    /* justify-content: center; */
    -ms-overflow-style: none;  
    scrollbar-width: none; 
}

.commentsection::-webkit-scrollbar{
    display:none;
}

.box{
    display:flex;
    justify-content: center;
    
    /* height:5rem; */
    /* width:40vw; */
    margin:0;
    
}

.smallbox{
    margin:0.5rem;
    /* border-top:2px solid black; */
    
    border-radius:0.5rem;
    width:30rem;
    /* min-width:400px; */
    box-shadow: .05rem 0.05rem 0.3rem black;
    /* overflow-wrap: break-word; */
    /* word-wrap: break-word; */
    
}

/* @media screen and (max-width:700px){
    .smallbox{
        min-width: 40vw;
    }
} */

.messageArea{
    display:flex;
    /* background:white; */
    justify-content: center;
    /* border:5px solid #00a2ff; */
    border-bottom: none;
    border-top-right-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
}
.deleteContainer{
    padding:0 0.5rem;
    /* display:flex;
    justify-content: flex-end; */
    /* max-width: 30%; */
    
}
.deleteButton{
    background:transparent;
    border:none;
    font-weight: bold;
    font-size:1rem;
    /* color:darkred; */
    padding:0;
}


.text{
    padding:1rem;
    
    width:70%;
    min-height: 4rem;
}

.textmessage{
    word-wrap:break-word;
    white-space: pre-wrap;
}

@media screen and (max-height:650px) {
    
    .commentsection{
        height: 70vh;
    }
    
}
@media screen and (max-height:530px) {
    
    .commentsection{
        height: 60vh;
    }
    
}

@media screen and (max-height:400px) {
    
    .commentsection{
        height: 50vh;
    }
    .inputbox{
        padding-bottom:0.2rem;
    }
    .textarea{
        margin:0.1rem;
    }
    
}
@media screen and (min-width:950px) {
    .smallbox{
        max-width:30rem;
    }
    .commentsection{
        max-width: 35rem;
    }
    .messageArea{
        /* max-width: 25rem; */
    }
}
@media screen and (max-width:700px){
    .commentsection{
        width:80vw;
        /* min-width: 20vw; */
        /* padding:2rem 1rem; */
    }
    .smallbox{
        width: 70vw;
    }
    
    
}


@media screen and (max-width:500px){
    .commentsection{
        width: 90vw;
    }
    .smallbox{
        width: 80vw;
    }
    .box{
        /* width: 80vw; */
    }
    .text{
        padding:0.5rem;
        /* margin:auto; */
    }
    .deleteContainer{
        width:5vw;
    }

}

@media screen and (max-width:270px){
    
    .deleteContainer{
        margin:0;
    }
    .text{
        font-size: 0.8rem;
        padding:0;
    }
}

